<template>
  <div class = "header">
   <div class = "header-left">
     <span class = "iconfont">&#xe624;</span>
   </div>
   <div class = "header-input">
      <span class = "iconfont">&#xe632;</span>
     输入城市/景点/游玩主题
     </div>
     <router-link to = '/city'>
        <div class = "header-right">{{this.curCity}}
        <span class = "iconfont arrow-icon">&#xe600;</span>
        </div>
      </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'Header',
  computed: {
    ...mapState({curCity: 'city'})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
    @import '~styles/variable.styl'
    .header
      display:flex
      line-height: .86rem
      background: $bgColor
      color:#fff;
     .header-left
       width: .64rem
       float: left
       text-align: center
     .header-input
       flex:1
       height: .64rem
       line-height: .64rem
       color: #ccc
       padding-left: .2rem
       margin-top: .12rem
       margin-left: .2em
       background: #fff
       border-radius: .1rem
     .header-right
       min-width: 1rem
       padding 0 .05rem
       float: right
       color #fff
       text-align: center
       .arrow-icon
        color #fff
        margin-left: -.04rem
        font-size: .24rem
</style>



