<template>
  <div>
    <banner
     :bannerName = "bannerName"
     :bannerImg = "bannerImg"
     :galleryImgs = "galleryImgs"
    ></banner>
    <detail-header></detail-header>
    <detail-list :tickets = 'lists'></detail-list>
    <div class="div"></div>
  </div>
</template>

<script>
import axios from 'axios'
import Banner from '@/detail/commponents/banner'
import DetailHeader from '@/detail/commponents/detailHeader'
import DetailList from '@/detail/commponents/detailList'
export default {
  name: 'Detail',
  data () {
    return {
      bannerName: '',
      bannerImg: '',
      galleryImgs: [],
      lists: []
    }
  },
  components: {
    Banner,
    DetailHeader,
    DetailList
  },
  methods: {
    getDetail () {
      axios.get('/api/detail.json', { params: {
        id: this.$route.params.id
      }}).then(this.getDetailSucc)
    },
    getDetailSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        this.bannerName = res.data.sightName
        this.bannerImg = res.data.bannerImg
        this.galleryImgs = res.data.galleryImgs
        this.lists = res.data.categoryList
      }
    }
  },
  mounted () {
    this.getDetail()
  }
}
</script>

<style lang='stylus' scoped>
  .div
   height 30rem
</style>



