<template>
  <div>
    <div class="detailsTicket" v-for = "(item, index) of tickets" :key="index">
        <span class="mp-ticketype-ticket"></span>
        {{item.title}}
        <div v-if="item.children">
            <detail-list :tickets = 'item.children'></detail-list>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detailList',
  props: {
    tickets: Array
  }
}
</script>

<style lang='stylus' scoped>
  .detailsTicket
   line-height .8rem
   padding 0rem .2rem
   .mp-ticketype-ticket
    display: inline-block
    width: .36rem
    height: .36rem
    background: url(//s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
    margin-right: .1rem;
    background-size: .4rem 3rem
</style>



