<template>
  <div>
   <home-header></home-header>
   <swiper :swiperList = 'swiperList'></swiper>
   <icons :iconList = 'iconList'></icons>
   <recommend :recommendList = 'recommendList'></recommend>
   <weekgo :weekendList = 'weekendList'></weekgo>
  </div>
</template>

<script>
import HomeHeader from '@/components/pages/Header'
import Swiper from '@/components/pages/swiper'
import Icons from '@/components/pages/icons'
import Recommend from '@/components/pages/recommend'
import Weekgo from '@/components/pages/weekgo'
import axios from 'axios'
import { mapState } from 'vuex'

export default {
  name: 'Home',
  data () {
    return {
      swiperList: [],
      iconList: [],
      recommendList: [],
      weekendList: [],
      lastCity: ''
    }
  },
  components: {
    HomeHeader,
    Swiper,
    Icons,
    Recommend,
    Weekgo
  },
  methods: {
    getHomeInfo () {
      axios.get('/api/index.json?city = ' + this.city)
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        this.swiperList = res.data.swiperList
        this.iconList = res.data.iconList
        this.recommendList = res.data.recommendList
        this.weekendList = res.data.weekendList
      }
    }
  },
  mounted () {
    this.lastCity = this.city
    this.getHomeInfo()
  },
  activated () {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city
      this.getHomeInfo()
    }
  },
  computed: {
    ...mapState(['city'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



