<template>
  <div class="gallery-container" @click="closeClick">
      <div class="wrapper">
          <!-- <div class="gallery-img-item">
              <img src="http://img1.qunarzz.com/sight/p0/201404/23/04b92c99462687fa1ba45c1b5ba4ad77.jpg_800x800_70debc93.jpg" alt="">
          </div> -->
            <swiper :options="swiperOption">
        <!-- slides -->
              <swiper-slide v-for = "(item, index) of galleryImgs" :key="index">
                <div class="gallery-img-item">
                  <img :src="item" alt="">
                </div>
             </swiper-slide>
        <!-- Optional controls -->
             <div class="swiper-pagination"  slot="pagination"></div>
         </swiper>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Gallery',
  props: {
    galleryImgs: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction',
          observer: true,
          observeParents: true
        },
        loop: true
      }
    }
  },
  methods: {
    closeClick () {
      this.$emit('closeClick')
    }
  }
}
</script>

<style lang='stylus' scoped>
    .gallery-container >>> .swiper-container
     overflow inherit
    .gallery-container
     display flex
     z-index 2
     flex-direction column
     justify-content center
     position  fixed
     left 0
     top 0
     right 0
     bottom 0
     color #fff
     background #000
     .wrapper
      height 0
      width 100%
      padding-bottom 100%
      .gallery-img-item img
       width 100%
      .swiper-pagination
       color #fff
       bottom -1rem
</style>



