<template>
  <div>
    <div class="banner-contain" @click="ishowClick">
        <img class="img" :src = 'bannerImg' alt="">
        <div class="banner-des">
            <div class="title">{{this.bannerName}}</div>
            <div class="img-info">
                <span class="iconfont">&#xe625;</span>
                <em>{{this.galleryImgs.length}}</em>
            </div>
        </div>
    </div>
    <fade-Animation>
      <galler v-if = "ishow" @closeClick = 'closeG' :galleryImgs = 'galleryImgs'></galler>
    </fade-Animation>
  </div>
</template>

<script>
import Galler from '@/gallery/gallery'
import FadeAnimation from '@/fade/fadeAnimation'
export default {
  name: 'Banner',
  props: ['bannerImg', 'bannerName', 'galleryImgs'],
  data () {
    return {
      ishow: false
    }
  },
  components: {
    Galler,
    FadeAnimation
  },
  methods: {
    ishowClick () {
    //   alert(64)
      this.ishow = true
    },
    closeG () {
      this.ishow = false
    }
  }
}
</script>

<style lang='stylus' scoped>
    .banner-contain
     position relative
     overflow hidden
     height 0
     padding-bottom 55%
     .banner-back
      position absolute
      left .1rem
      top .1rem
      display block
      width .72rem
      height .72rem
      background #000
      opacity .5
      color #fff
      text-align center
      border-radius .36rem
      .banner-back-icon
       line-height .72rem
     .img
      width 100%
     .banner-des
      display flex
      height .8rem
      background red
      overflow auto
      .title
       position absolute
       left .2rem
       right .2rem
       bottom .36rem
       font-size .4rem
       color #fff
       flex 1
      .img-info
       position absolute
       left .2rem
       bottom .86rem
       width 1.2rem
       height .4rem
       line-height .4rem
       color #ffffff
       background rgba(0,0,0,.5)
       border-radius .2rem
       font-size .24rem
       text-align center
</style>



