<template>
  <div class="list" ref="wrap">
      <div>
        <!-- 你的位置 -->
        <div class = "city-list-title">您的位置</div>
            <div class="city-list">
                <div class = 'list-city-area'>
                <div class="city-list-item">{{this.curCity}}</div>
            </div>
            </div>
        <!-- 热门分类 -->
        <div class = "city-list-title">热门城市</div>
        <div class="city-list">
             <div class = 'list-city-area'
               v-for = 'item of hotCities'
               :key = 'item.id'
               @click = 'changeMutations(item.name)'
               >
                <div class="city-list-item">{{item.name}}</div>
            </div>
        </div>
        <!-- A-Z城市分类 -->
        <div v-for = '(item, key) of cities' :key = 'key' :ref = 'key'>
            <div class = "city-list-title" >{{key}}</div>
            <div class="city-item-choose">
                <div class="city-name border-bottom"
                 v-for = 'city of item' :key = 'city.id'
                @click = 'changeMutations(city.name)'
                >
                {{city.name}}
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'cityList',
  props: {
    hotCities: Array,
    cities: Object,
    letter: String
  },
  methods: {
    changeMutations (city) {
      // this.$store.commit('newCity', city)
      this.newCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['newCity'])
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrap)
  },
  watch: {
    letter () {
      if (this.letter) {
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
        // debugger
        // console.log(this.$refs[this.letter])
      }
    }
  },
  computed: {
    ...mapState({curCity: 'city'})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '~styles/variable.styl'
   .list
    overflow hidden
    position absolute
    left 0
    right 0
    top 1.76rem
    bottom 0
    left
    .city-list-title
     height .5rem
     line-height .5rem
     padding-left .2rem
     font-size .26rem
     background #eeeeee
    .city-list
     overflow hidden
     padding .1rem .6rem .1rem .1rem
     .list-city-area
      float left
      width 33.3%
      .city-list-item
       padding .1rem
       margin .1rem
       border-radius .06rem
       border 1px solid #eee
       box-sizing border-box
       text-align center
    .city-item-choose
     margin-top .1rem
     .city-name
      line-height .76rem
      padding-left .2rem
     .border-bottom
      border-bottom 1px solid #eee
</style>



