<template>
    <div class="container">
        <div class="rec-title">周末去哪儿</div>
        <div class="rec-container" v-for = "item of weekendList" :key = "item.id">
            <div class="rec-list-arear">
                <img class="rec-img" :src="item.imgUrl" alt="">
                <div class="rec-list-des">
                    <p class="p1">{{item.title}}</p>
                    <p>{{item.desc}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'Recommend',
  props: {
    weekendList: Array
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '~styles/variable.styl'
    .container
      .rec-title
        height .6rem
        line-height .7rem
        font-weight bold
        margin-bottom .2rem
        background #eee
      .rec-container
        margin-bottom .2rem
        .rec-list-arear
         .rec-img
          width 100%
          height 2rem
         .rec-list-des
          margin-left .2rem
         .rec-list-des p:nth-of-type(1)
          line-height .6rem
         .rec-list-des p:nth-of-type(2)
          line-height .4rem
          color #cccccc
</style>



