<template>
    <div class="icons">
      <swiper :options="swiperOption">
        <swiper-slide v-for = "(item, key) of pages" :key = key>
        <div class="icon" v-for = "val of item" :key = "val.id">
            <div class = "icon-img-content">
                <img class="icon-img" :src="val.imgUrl" alt="">
                <div class="des">{{val.desc}}</div>
            </div>
        </div>
        </swiper-slide>
        </swiper>
        </div>
</template>

<script>

export default {
  name: 'Icons',
  props: ['iconList'],
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const contents = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!contents[page]) {
          contents[page] = []
        }
        contents[page].push(item)
      })
      return contents
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '~styles/variable.styl'
  .icons
    overflow: hidden
    height: 0
    padding-bottom: 46%
    .icon
      position relative
      overflow: hidden
      float left
      width:25%
      height: 0
      padding-bottom: 25%
     .icon-img-content
       position absolute
       top:0
       left 0
       right 0
       bottom .84rem
       .icon-img
         display block
         height 100%
         margin 0 auto
       .des
         text-align center
         height: .44rem
         line-height .44rem
         ellipsis()
</style>



