<template>
  <div>
    <ul class="city-letter">
      <li class="letter-item"
        v-for = '(index,key) of cities'
        :key = 'key'
        :ref = 'key'
        @click = "change"
        @touchstart = 'handleTouchStart'
        @touchmove = 'handleTouchMove'
        @touchend = 'handleTouchEnd'
        >
        {{key}}
        </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'cityLetter',
  props: {
    cities: {}
  },
  data () {
    return {
      touchStatus: false,
      startY: 0,
      timer: null
    }
  },
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  methods: {
    change (e) {
      // 不能用innerHTML,获取的内容包含换行符
      this.$emit('changeLetter', e.target.innerText)
      // console.log(this.letters)
    },
    handleTouchStart () {
      this.touchStatus = true
    },
    handleTouchMove (e) {
      if (this.touchStatus) {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        setTimeout(() => {
          const touchY = e.touches[0].clientY - 88
          const index = Math.floor((touchY - this.startY) / 20)
          if (index >= 0 && index < this.letters.length) {
            this.$emit('changeLetter', this.letters[index])
          }
        }, 16)
      }
    },
    handleTouchEnd () {
      this.touchStatus = false
    }
  },
  computed: {
    letters () {
      const letterArray = []
      for (var item in this.cities) {
        letterArray.push(item)
      }
      return letterArray
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/variable.styl'
  .city-letter
   display flex
   flex-direction column
   justify-content center
   position absolute
   top 1.78rem
   right 0
   bottom 0
   width .4rem
   .letter-item
    line-height .4rem
    text-align center
    color $bgColor
</style>



