<template>
  <div>
    <city-header></city-header>
    <city-search :cities = 'cities'></city-search>
    <city-list :hotCities = 'hotcities' :cities = 'cities' :letter = 'letter'></city-list>
    <city-letter :cities = 'cities' @changeLetter = 'getLetter'></city-letter>
  </div>
</template>

<script>
import cityHeader from '@/city/commponents/cityHeader'
import citySearch from '@/city/commponents/citySearch'
import cityList from '@/city/commponents/cityList'
import cityLetter from '@/city/commponents/cityLetter'
import aixos from 'axios'
export default {
  name: 'City',
  data () {
    return {
      hotcities: [],
      cities: {},
      letter: ''
    }
  },
  components: {
    cityHeader,
    citySearch,
    cityList,
    cityLetter
  },
  methods: {
    getCityInfo () {
      aixos.get('/api/city.json').then(this.getCityInfoSucc)
    },
    getCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.hotcities = data.hotCities
        this.cities = data.cities
        // console.log(this.cities)
      }
    },
    getLetter (data) {
      this.letter = data
      // console.log(data)
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
</style>



