<template>
  <div>
     <div class = "city-header-input">
      <input class="search-input" type="text" placeholder="输入城市名或拼音" v-model="keyWords">
     </div>
     <!-- 搜索框下面的面板 -->
     <div class = "search-logic-panner" v-show = 'keyWords'>
       <ul>
         <li class="border-bottom"
          v-for = 'item of searchValueArr'
           :key = 'item.id'
           @click = 'changeMutations(item)'
           >
           {{item}}
          </li>
          <li class="border-bottom" v-show = "isShow">没有匹配项</li>
       </ul>
     </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'citySearch',
  data () {
    return {
      keyWords: '',
      searchValueArr: [],
      timer: null
    }
  },
  props: {
    cities: Object
  },
  computed: {
    isShow () {
      return !this.searchValueArr.length
    }
  },
  methods: {
    changeMutations (city) {
      // this.$store.commit('newCity', city)
      this.newCity(city)
      this.$router.push('/')
      this.keyWords = ''
    },
    ...mapMutations(['newCity'])
  },
  watch: {
    keyWords () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          for (let j = 0; j < this.cities[i].length; j++) {
            if (
              this.cities[i][j].spell.indexOf(this.keyWords) > -1 ||
              this.cities[i][j].name.indexOf(this.keyWords) > -1
            ) {
              result.push(this.cities[i][j].spell && this.cities[i][j].name)
            }
          }
        }
        // console.log(result)
        this.searchValueArr = result
      }, 20)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
@import '~styles/variable.styl'

.city-header-input {
  height: 0.72rem
  line-height: 0.72rem
  color: #ccc
  padding: 0.1rem
  background: $bgColor

  .search-input {
    width: 100%
    height: 0.62rem
    text-align: center
    padding: 0.2rem
    box-sizing: border-box
    border-radius: 0.06rem
    color: #666
  }
}

.search-logic-panner {
  z-index: 1
  position: absolute
  top: 1.76rem
  left: 0
  right: 0
  bottom: 0
  background: #eee

  .border-bottom {
    height: 0.8rem
    line-height: 0.8rem
    background: #ffffff
    padding-left: 0.2rem
    border-bottom: 0.02rem solid #ccc
  }
}
</style>



