<template>
  <div>
    <router-link to = '/' class="banner-back" tag = "div"  v-show="!ishow">
      <div class="iconfont banner-back-icon">&#xe624;</div>
    </router-link>
    <!-- header -->
    <div class = "header" v-show="ishow" :style="opacityStyle">
      <div class = "header-left">
        <router-link to = '/'>
         <span class = "iconfont icon">&#xe624;</span>
        </router-link>
        <span class="title">景点详情</span>
      </div>
    </div>
    <!-- <div class="div"></div> -->
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      ishow: false,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = {opacity: opacity}
        this.ishow = true
      } else {
        this.ishow = false
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang='stylus' scoped>
@import '~styles/variable.styl'
    .banner-back
     position absolute
     left .2rem
     top .2rem
     width .64rem
     height .64rem
     line-height .64rem
     border-radius .32rem
     text-align center
     color #fff
     background rgba(0,0,0,.5)
    .header
      position fixed
      left 0
      top 0
      width: 100%
      line-height: .64rem
      background: $bgColor
      color:#fff;
     .header-left
       text-align center
       margin-left .1rem
     .icon
      float left
      color #fff
     .title
      text-align center
</style>



